import { GetServerSideProps } from "next";
import { getService } from "../../services/get-service";
import { getBlogs } from "../../interfaces";
import CategoriesInterface from "../../interfaces/categories-interface";

interface GetDeaileProps {
    blogs: getBlogs[];
    getCategories: CategoriesInterface[];
    slideBlogs: getBlogs[];
}

const GetDeaileSlug = ({
    blogs,
    getCategories,
    slideBlogs,
}: GetDeaileProps) => {
    console.log(blogs);

    return (
        <Box key={item.id} sx={{ display: "flex", gap: 2, mt: "10px" }}>
            <Image
                src={item.image.url}
                alt={item.title}
                width={180}
                height={110}
                style={{ borderRadius: "10px" }}
            />
            <Box>
                <Typography variant="body1">{item.title}</Typography>
                <Typography>date: {format(new Date(), "MM/dd/yyy")}</Typography>
            </Box>
        </Box>
    );
};

export default GetDeaileSlug;

export const getServerSideProps: GetServerSideProps<GetDeaileProps> = async ({
    query,
}) => {
    try {
        console.log(query);

        const blogs = await getService.getDeaileSlug(query.slug as string);
        const categories = await getService.getCategories();
        const slideBlogs = await getService.getSlideBlogs();
        const safeBlogs = blogs;
        return {
            props: {
                blogs: safeBlogs,
                getCategories: categories,
                slideBlogs: slideBlogs,
            },
        };
    } catch (error) {
        console.error("❌ Xato:", error);
        return {
            props: {
                blogs: [],
                getCategories: [],
                slideBlogs: [],
            },
        };
    }
};
